.Logo {
  width: 100%;
  display: block;
  color: white;
  fill: currentColor; }
  .Logo, .Logo-bomb-warning {
    -webkit-transition: color .5s, fill .5s;
    -o-transition: color .5s, fill .5s;
    transition: color .5s, fill .5s; }
  .Logo-bomb-warning {
    color: #ffde42;
    -webkit-transform-origin: 9.9057% 50%;
        -ms-transform-origin: 9.9057% 50%;
            transform-origin: 9.9057% 50%; }
  .Logo:hover, .Logo:focus, .Logo:active {
    color: #ffde42; }
    .Logo:hover .Logo-bomb-warning, .Logo:focus .Logo-bomb-warning, .Logo:active .Logo-bomb-warning {
      -webkit-animation: spin 1.5s infinite linear, colorFade 2s infinite linear;
              animation: spin 1.5s infinite linear, colorFade 2s infinite linear;
      -webkit-animation-duration: 1.5s;
              animation-duration: 1.5s;
      -webkit-animation-iteration-count: infinite;
              animation-iteration-count: infinite;
      -webkit-animation-timing-function: linear;
              animation-timing-function: linear; }

@-webkit-keyframes spin {
  0% {
    -webkit-transform: rotate(0);
            transform: rotate(0); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes spin {
  0% {
    -webkit-transform: rotate(0);
            transform: rotate(0); }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@-webkit-keyframes colorFade {
  0% {
    color: #ffde42; }
  50% {
    color: #ca1c05; }
  100% {
    color: #ffde42; } }

@keyframes colorFade {
  0% {
    color: #ffde42; }
  50% {
    color: #ca1c05; }
  100% {
    color: #ffde42; } }



